<template>
  <div class="account-application-detail">
    <el-new-card v-loading="isLoading">
      <template slot="header">
        <div>
          {{ $route.meta.name }}
          <acct-appl-sts-icon :status="info.status" point></acct-appl-sts-icon>
        </div>
      </template>

      <div>
        <h5 class="sub-title">账户信息</h5>
        <info-block>
          <info-item label="积分账户名称：">{{ info.account }}</info-item>
          <info-item label="部门/企业名称：">{{ info.deptName }}</info-item>
          <info-item label="类型：">{{ info.type }}</info-item>
          <info-item label="所属：">{{ info.ownership }}</info-item>
          <info-item label="联系人：">{{ info.contact }}</info-item>
          <info-item label="手机号：">{{ info.mobile }}</info-item>
          <info-item label="邮箱：">{{ info.email }}</info-item>
        </info-block>
        <info-block>
          <info-item label="申请人：">{{ info.creater || '-' }}</info-item>
          <info-item label="申请时间：">{{ info.createTime || '-' }}</info-item>
          <info-item label="编辑人：">{{ info.editer || '-' }}</info-item>
          <info-item label="编辑时间：">{{ info.editTime || '-' }}</info-item>
          <info-item label="审批时间：">{{ info.updateTime || '-' }}</info-item>
        </info-block>

        <div class="btns">
          <el-button icon="el-icon-s-order" size="small" @click="popAttachments">查看附件</el-button>
          <option-btns
            :status="isAppling"
            :auth="optionsAuth"
            :aid="id"
            verify="serviceVerifyAppl"
            @onEdit="toEditAccount"
            @onCancel="changeCancel"
            @onReject="changeNoPass"
            @onPass="changePass"
            @onClose="goBack"
          ></option-btns>
        </div>

        <el-divider></el-divider>

        <h5 class="sub-title">申请额度</h5>
        <amount-config :data="info"></amount-config>
      </div>
    </el-new-card>

    <popup-atta :visible.sync="attaVisible" :fileList="info.attachments"></popup-atta>
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import InfoBlock from '@/components/InfoBlock'
import InfoItem from '@/components/InfoItem'
import OptionBtns from '@/components/OptionBtns'
import AmountConfig from '@/components/amounts/AmountConfig'
import AcctApplStsIcon from '@/components/status/AcctApplStsIcon'
import PopupAtta from '@/views/popups/PopupAtta'
import { mapGetters } from 'vuex'

export default {
  name: 'account-application-detail',
  components: {
    ElNewCard,
    InfoBlock,
    InfoItem,
    OptionBtns,
    AmountConfig,
    AcctApplStsIcon,
    PopupAtta
  },
  data () {
    return {
      id: '',
      info: {
        status: -1,
        account: '',
        type: 0,
        ownership: '',
        deptId: '',
        deptName: '',
        contact: '',
        mobile: '',
        email: '',
        budget: '',
        budgetAmount: '',
        budgetAmountLine: '',
        imprest: '',
        imprestAmount: '',
        imprestAmountLine: ''
      },
      isLoading: false,
      attaVisible: false
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    }),

    isAppling () {
      if (this.info.status === 0 || this.info.status === 3) return 'isAppling'
      return ''
    },

    optionsAuth () {
      // 判断是否可编辑或者管理员
      if (this.accountInfo.isAdmin) return 'isAdmin'
      if (this.info.deptId === this.accountInfo.orgId) return 'isEditor'
      return ''
    }
  },
  created () {
    this.id = this.$route.query.id
    if (this.id) {
      this.getDetail(this.id)
    } else {
      this.$router.replace('/404')
    }
  },
  methods: {
    getDetail (id) {
      this.isLoading = true
      this.$http.serviceGetAcctApplDetail({ id })
        .then(res => {
          this.info = { ...res }
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 跳转到账户申请编辑
    toEditAccount () {
      this.$router.push(`/crdtAcctMgmt/editAcctAppl?id=${this.id}`)
    },

    // 审批通过
    changePass () {
      this.changeApplicationStatus(0)
    },

    // 拒绝申请
    changeNoPass () {
      this.changeApplicationStatus(3)
    },

    // 取消申请
    changeCancel () {
      this.changeApplicationStatus(4)
    },

    // 更新申请状态
    changeApplicationStatus (status) {
      this.isLoading = true
      this.$http.serviceUpdateAcctApplSts({ status })
        .then(res => {
          this.info.status = status
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 返回
    goBack () {
      this.$router.go(-1)
    },

    // 查看附件
    popAttachments () {
      this.attaVisible = true
    }
  }
}
</script>

<style lang="scss" scoped>
.account-application-detail {
  .btns {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
</style>
